import React from "react"
import Giscus from "@giscus/react"
import { useTheme } from "../theme"

interface GiscusCommentsProps {
  repo?: `${string}/${string}`
  repoId?: string
  category?: string
  categoryId?: string
}

/**
 * Giscus Comments Component - Digital Garden Theme
 *
 * This component integrates Giscus (GitHub Discussions-based comments)
 * with the Digital Garden theme, automatically syncing the theme
 * between light and dark modes.
 *
 * Default configuration uses the blog's repository.
 * The component applies glassmorphism styling to match the Digital Garden aesthetic.
 */
export function GiscusComments({
  repo = "ximing/blog",
  repoId = "MDEwOlJlcG9zaXRvcnkxMjM0NTY3ODA=",
  category = "Comments",
  categoryId = "DIC_kwDOB12345",
}: GiscusCommentsProps) {
  const { theme, isDark } = useTheme()

  // Map our theme to Giscus theme
  // Light mode uses 'light', dark mode uses 'dark'
  const giscusTheme = isDark ? "dark" : "light"

  return (
    <div
      className="giscus-wrapper rounded-xl bg-bg-secondary p-4 sm:p-6 shadow-sm"
      style={{
        borderRadius: "16px 20px 14px 22px",
      }}
    >
      {/* Section Header */}
      <div className="flex items-center gap-2 mb-6">
        <svg
          className="w-5 h-5 text-accent"
          fill="currentColor"
          viewBox="0 0 16 16"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path d="M1.75 1h8.5c.966 0 1.75.784 1.75 1.75v5.5A1.75 1.75 0 0 1 10.25 10H7.061l-2.574 2.573A1.458 1.458 0 0 1 2 11.543V10h-.25A1.75 1.75 0 0 1 0 8.25v-5.5C0 1.784.784 1 1.75 1ZM1.5 2.75v5.5c0 .138.112.25.25.25h1a.75.75 0 0 1 .75.75v2.19l2.72-2.72a.749.749 0 0 1 .53-.22h3.5a.25.25 0 0 0 .25-.25v-5.5a.25.25 0 0 0-.25-.25h-8.5a.25.25 0 0 0-.25.25Zm13 2a.25.25 0 0 0-.25-.25h-.5a.75.75 0 0 1 0-1.5h.5c.966 0 1.75.784 1.75 1.75v5.5A1.75 1.75 0 0 1 14.25 12H14v1.543a1.458 1.458 0 0 1-2.487 1.03L9.22 12.28a.75.75 0 0 1 .53-1.28h3.5a.25.25 0 0 0 .25-.25Z" />
        </svg>
        <h3
          className="text-lg font-semibold text-text-primary"
          style={{
            fontFamily: '"Noto Serif SC", Georgia, "Times New Roman", serif',
          }}
        >
          评论与讨论
        </h3>
      </div>

      {/* Giscus Widget */}
      <Giscus
        repo={repo}
        repoId={repoId}
        category={category}
        categoryId={categoryId}
        mapping="pathname"
        strict="0"
        reactionsEnabled="1"
        emitMetadata="0"
        inputPosition="top"
        theme={giscusTheme}
        lang="zh-CN"
        loading="lazy"
      />
    </div>
  )
}

export default GiscusComments
