import 'katex/dist/katex.min.css'
import React from "react"
import { Link, graphql, PageProps } from "gatsby"
import { ArrowLeft, Clock, Calendar } from "@phosphor-icons/react"
import Bio from "../components/bio"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { TableOfContents } from "../components/blog"
import { GiscusComments } from "../components/comments"
import { useCodeCopy } from "../hooks"

interface BlogPostPageContext {
  slug: string
  previous?: {
    fields: {
      slug: string
    }
    frontmatter: {
      title: string
    }
  }
  next?: {
    fields: {
      slug: string
    }
    frontmatter: {
      title: string
    }
  }
}

interface BlogPostData {
  site: {
    siteMetadata: {
      title: string
      author: string
    }
  }
  markdownRemark: {
    id: string
    excerpt: string
    html: string
    tableOfContents: string
    wordCount: {
      words: number
      paragraphs: number
      sentences: number
    }
    fields: {
      slug: string
    }
    frontmatter: {
      title: string
      date: string
      description?: string
      tags?: string[]
    }
  }
}

const BlogPostTemplate: React.FC<
  PageProps<BlogPostData, BlogPostPageContext>
> = ({ data, pageContext, location }) => {
  const post = data.markdownRemark
  const siteTitle = data.site.siteMetadata.title
  const { previous, next } = pageContext

  // Calculate reading time (assuming 200 words per minute)
  const readingTime = Math.ceil(post.wordCount.words / 200)

  // Add copy buttons to code blocks
  useCodeCopy()

  return (
    <article>
      <Layout location={location} title={siteTitle}>
        <SEO
          title={post.frontmatter.title}
          description={post.frontmatter.description || post.excerpt}
        />

        {/* Breadcrumb Navigation */}
        <nav className="fade-in mb-6" aria-label="Breadcrumb">
          <Link
            to="/"
            className="inline-flex items-center text-xs text-text-muted hover:text-accent transition-colors duration-200 group"
          >
            <ArrowLeft className="w-3.5 h-3.5 mr-1 transition-transform duration-200 group-hover:-translate-x-1" weight="duotone" />
            返回首页
          </Link>
        </nav>

        {/* Article Header */}
        <header className="mb-8 fade-in">
          {/* Title - Noto Serif SC */}
          <h1
            className="text-xl sm:text-2xl lg:text-3xl font-bold text-text-primary leading-tight mb-4"
            style={{ fontFamily: '"Noto Serif SC", Georgia, "Times New Roman", serif' }}
          >
            {post.frontmatter.title}
          </h1>

          {/* Meta Information Row */}
          <div className="flex flex-wrap items-center gap-3 text-xs text-text-muted">
            {/* Date */}
            <div className="flex items-center gap-1.5">
              <Calendar className="w-4 h-4 text-text-subtle" weight="duotone" />
              <time>{post.frontmatter.date}</time>
            </div>

            {/* Reading Time */}
            <div className="flex items-center gap-1.5">
              <Clock className="w-4 h-4 text-text-subtle" weight="duotone" />
              <span>{readingTime} 分钟阅读</span>
            </div>

            {/* Tags */}
            {post.frontmatter.tags && post.frontmatter.tags.length > 0 && (
              <>
                <span className="text-text-muted">·</span>
                <div className="flex flex-wrap gap-1.5">
                  {post.frontmatter.tags.map((tag) => (
                    <Link
                      key={tag}
                      to={`/tags/${tag.toLowerCase().replace(/\s+/g, "-")}/`}
                      className="inline-flex items-center px-2 py-0.5 text-[11px] font-medium text-accent bg-accent/10 rounded-full hover:bg-accent hover:text-accent-foreground transition-colors duration-200"
                      style={{ borderRadius: '12px 16px 10px 18px' }}
                    >
                      #{tag}
                    </Link>
                  ))}
                </div>
              </>
            )}
          </div>

        </header>

        {/* Article Content - Noto Sans SC */}
        <div
          className="prose max-w-none fade-in article-content text-text-primary"
          style={{
            fontFamily: '"Noto Sans SC", system-ui, -apple-system, sans-serif',
          }}
          dangerouslySetInnerHTML={{ __html: post.html }}
        />

        {/* Reading Stats & Divider */}
        <hr className="my-8 border-text-muted/20" />
        <div
          id={post.fields.slug}
          className="leancloud_visitors flex justify-between items-center text-xs text-text-muted mb-8"
          data-flag-title={post.frontmatter.title}
        >
          <span className="font-medium">
            共 {post.wordCount.words} 字 · {post.wordCount.paragraphs} 段落
          </span>
          <span>
            <em className="post-meta-item-text not-italic">阅读量 </em>
            <i className="leancloud-visitors-count font-medium text-accent"></i>
          </span>
        </div>

        {/* Author Bio */}
        <div className="mb-10">
          <Bio />
        </div>

        {/* Previous/Next Navigation */}
        <nav
          className="grid grid-cols-1 sm:grid-cols-2 gap-3 mb-10"
          aria-label="文章导航"
        >
          {/* Previous Post */}
          <div className="sm:col-start-1">
            {previous && (
              <Link
                to={previous.fields.slug}
                rel="prev"
                className="group block p-3.5 sm:p-4 rounded-xl bg-bg-secondary hover:bg-bg-tertiary transition-all duration-200 min-h-[40px]"
              >
                <span className="text-[11px] text-text-muted mb-1.5 block flex items-center gap-1">
                  <ArrowLeft className="w-3 h-3" weight="bold" />
                  上一篇
                </span>
                <span className="text-xs font-medium text-text-primary group-hover:text-accent transition-colors line-clamp-2">
                  {previous.frontmatter.title}
                </span>
              </Link>
            )}
          </div>

          {/* Next Post */}
          <div className="sm:col-start-2 sm:text-right">
            {next && (
              <Link
                to={next.fields.slug}
                rel="next"
                className="group block p-3.5 sm:p-4 rounded-xl bg-bg-secondary hover:bg-bg-tertiary transition-all duration-200 min-h-[40px]"
              >
                <span className="text-[11px] text-text-muted mb-1.5 block flex items-center justify-end gap-1">
                  下一篇
                  <ArrowLeft className="w-3 h-3 rotate-180" weight="bold" />
                </span>
                <span className="text-xs font-medium text-text-primary group-hover:text-accent transition-colors line-clamp-2">
                  {next.frontmatter.title}
                </span>
              </Link>
            )}
          </div>
        </nav>

        {/* Comments - Giscus with Digital Garden theme */}
        <GiscusComments />
      </Layout>

      {/* Table of Contents Sidebar */}
      <TableOfContents html={post.tableOfContents} />
    </article>
  )
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        author
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt(pruneLength: 160)
      html
      fields {
        slug
      }
      tableOfContents
      frontmatter {
        title
        date(formatString: "YYYY年MM月DD日")
        description
        tags
      }
      wordCount {
        words
        paragraphs
        sentences
      }
    }
  }
`
